import React from 'react';
import './UploadBox.css';
import { Link } from 'react-router-dom';
import Cookies from 'js-cookie';
import md5 from 'blueimp-md5';
import axios from 'axios';
import * as qiniu from 'qiniu-js'

import Modal from '../../common/Modal';
import { generateSign, app_key, app_secret, version } from '../../config';

import up from '../../../assets/images/up.png';
import down from '../../../assets/images/down.png';
import upload from '../../../assets/images/upload.png';
import loading from '../../../assets/images/loading.png';

const Select = ({ 
        handleSelectClick, 
        pageSelectShow, 
        list, 
        currentSelect, 
        handleCurrentSelectChange
    }) => {
    const selectedStyle = {
        borderRadius: '0 0 5px 5px',
    }
    const arrowBgSelectedStyle = {
        backgroundColor: '#ffd900'
    }
    const midBtnStyle = pageSelectShow ? selectedStyle : null;
    const arrow = pageSelectShow ? down : up;
    return (
        <div className="music-box-pagination"
            style={{
                justifyContent: "flex-start",
                height: '100%',
                marginTop: 0
            }}
        >
            <div 
                className="page-mid upload-mid"
                style={{
                    width: '154px',
                }}
            >
                <div 
                    className="page-mid-select"
                    style={{
                        display: pageSelectShow ? 'flex' : 'none',
                        width: '154px',
                    }}
                >
                    {
                        list.map((item, index) => (
                            <span 
                                key={index} 
                                className="page-mid-option"
                                onClick={handleCurrentSelectChange(index)}
                            >{item}</span>
                        ))
                    }
                </div>
                <div 
                    className="page-mid-btn"
                    onClick={handleSelectClick}
                    style={midBtnStyle}
                >
                    <span className="page-mid-option">{list[currentSelect]}</span>
                    <span 
                        className="arrow-box"
                        style={arrowBgSelectedStyle}
                    >
                        <span
                            style={{
                                width: '18px',
                                height: '14px',
                                background: `url(${arrow}) center center/50% no-repeat`,
                            }}
                        >
                        </span>
                    </span>
                </div>
            </div>
        </div>
    )
}

class UploadBox extends React.Component {
    constructor() {
        super()
        this.state = {
            agree: false,
            file: undefined,
            uploadInfo: '',
            pageSelectShow: false,
            modalShow: false,
            modalInfo: '',
            musicName: '',
            musicNameWarn: '',
            singer: '',
            singerWarn: '',
            currentSelect: 0,
            selectWarn: '',
            fileWarn: '',
            percent: 0,
            isLoading: false,
            loadingTip: '',
            count: 3,
        }
        this.handleSelect = this.handleSelect.bind(this);
        this.handleMusicUpload = this.handleMusicUpload.bind(this);
        this.handleSelectListClick = this.handleSelectListClick.bind(this);
        this.handleUploadClick = this.handleUploadClick.bind(this);
        this.modalClose = this.modalClose.bind(this);
        this.handleMusicNameChange = this.handleMusicNameChange.bind(this);
        this.handleSingerChange = this.handleSingerChange.bind(this);
        this.validate = this.validate.bind(this);
    }

    componentWillMount() {
        const userToken = Cookies.get('userToken');
        if(!userToken) window.location.href = '/login';        
    }

    handleSelect() {
        this.setState({
            agree: !this.state.agree
        })
    }

    handleMusicUpload(e) {
        let fileWarn = '';
        let uploadInfo = '';
        if(!e.target.files[0]) {
            fileWarn = '';
            uploadInfo = '';
        }else {
            fileWarn = '';
            uploadInfo = e.target.files[0].name;
        }
        this.setState({
            file: e.target.files,
            uploadInfo,
            fileWarn,
        })
    }

    handleSelectListClick() {
        this.setState({
            pageSelectShow: !this.state.pageSelectShow
        })
    }

    validate() {
        const { agree, musicName, singer, file, currentSelect } = this.state;
        let canUpload = true;
        if(!agree) {
            this.setState({
                modalInfo: '',
                modalShow: true,
            })
            canUpload = false;
        }
        if(!musicName) {
            this.setState({
                musicNameWarn: ''
            })
            canUpload = false;
        }
        if(musicName.length > 20) canUpload = false;
        if(!singer) {
            this.setState({
                singerWarn: ''
            })
        }
        if(singer.length > 20) canUpload = false;
        if(!file) {
            this.setState({
                fileWarn: '',
                uploadInfo: '',
            })
            canUpload = false;
        }else{
            const size = file[0].size;
            const size_M = (size/1024/1024).toFixed(2);
            if(size_M > 20) {
                this.setState({
                    fileWarn: '20M',
                    uploadInfo: '',
                })
                canUpload = false;
            }
        }
        if(currentSelect === 0 || !currentSelect) {
            this.setState({
                selectWarn: ''
            })
            canUpload = false;
        }
        return canUpload;
    }

    // 
    handleUploadClick() {
        const canUpload = this.validate();
        if(!canUpload) return false;
        const user_token = Cookies.get('userToken');
        const userInfo = Cookies.getJSON('userInfo');
        const time = new Date().valueOf();
        const { file } = this.state;
        const size = file[0].size;
        const uid = userInfo && userInfo.uid;
        const from = 'web';
        const orig_sign = generateSign({
            app_key,
            app_secret,
            time,
            version,
            action: 'TOKEN-GENERATE',
            user_token:'',
        })
        const sign = md5(orig_sign + time);
        axios({
            method: 'post',
            url: 'http://47.98.101.206:3080/service/image/token-generate.htm',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            params: {
                action: 'TOKEN-GENERATE',
                app_key,
                time,
                size: 1,
                version,
                sign,
                type: 2,
                from,
            }
        })
        .then(response => {
            const {data} = response;
            if(data.code === 200) {
                return new Promise((resolve, reject) => resolve(data.data.results))
            }else{
                this.setState({
                    modalInfo: '',
                    modalShow: true,
                })
            }
        })
        .then(data => {
            const { attachName, token } = data[0];
            const file = this.state.file[0];
            let key = '';
            return new Promise((resolve, reject) => {
                this.setState({
                    isLoading: true,
                    loadingTip: '',
                })
                const observer = {
                    next: (res) => {
                        this.setState({
                            percent: res.total.percent  > 99 ? 99 : res.total.percent,
                        })
                    },
                    err: (err) => {
                        reject(err)
                    },
                    complete: (res) => {
                        key = res.key;
                        this.setState({
                            percent: 99,
                        })
                        resolve(key);
                    }
                }
                const observable = qiniu.upload(file, attachName, token);
                observable.subscribe(observer);
            })
        })
        .then(key => {
            const { musicName, singer, currentSelect } = this.state;
            const orig_sign = generateSign({
                app_key,
                app_secret,
                time,
                version,
                action: 'MUSIC-INSERT',
                user_token,
            })
            const sign = md5(orig_sign + time);
            return axios({
                method: 'post',
                url: 'http://47.98.101.206:3080/service/music/music-insert.htm',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                },
                params: {
                    action: 'MUSIC-INSERT',
                    app_key,
                    user_token,
                    name: musicName,
                    address: key,
                    singer,
                    uid,
                    length: 1,
                    time,
                    size: (size/1024/1024).toFixed(2),
                    version,
                    sign,
                    type: currentSelect,
                    from,
                }
            })
        })
        .then(response => {
            if(response.data.code === 200) {
                this.setState({
                    percent: 100,
                })
                this.countDown();
            }else {
                this.setState({
                    modalInfo: response.data.message,
                    modalShow: true,
                })
            }
        })
        .catch(e => {
            console.log('e', e)
        })
    }

    countDown() {
        const timer = setTimeout(() => {
            if(this.state.count <= 0) {
                this.setState({
                    count: 3,
                    agree: false,
                    file: undefined,
                    uploadInfo: '',
                    musicName: '',
                    musicNameWarn: '',
                    singer: '',
                    singerWarn: '',
                    currentSelect: 0,
                    selectWarn: '',
                    fileWarn: '',
                    percent: 0,
                    isLoading: false,
                    loadingTip: '',
                })
                return clearTimeout(timer);
            }
            this.setState({
                count: this.state.count - 1,
                loadingTip: `${this.state.count}s`,
            })
            this.countDown();
        }, 1000)
    }

    modalClose() {
        this.setState({
            modalInfo: '',
            modalShow: false,
        })
    }

    handleMusicNameChange(e) {
        let musicNameWarn = '';
        if(!e.target.value) musicNameWarn = '';
        if(e.target.value.length > 20) musicNameWarn = '20';
        this.setState({
            musicName: e.target.value,
            musicNameWarn,
        })
    }

    handleSingerChange(e) {
        let singerWarn = '';
        if(!e.target.value) singerWarn = '';
        if(e.target.value.length > 20) singerWarn = '20';
        this.setState({
            singer: e.target.value,
            singerWarn,
        })
    }

    handleCurrentSelectChange = (index) => (e) => {
        let selectWarn = '';
        if(index === 0) selectWarn = '';
        this.setState({
            currentSelect: index,
            pageSelectShow: !this.state.pageSelectShow,
            selectWarn,
        })
    }

    render() {
        return <div className="upload-wrap">
            <div className="upload-box">
                <Link className="to-music-list" to="/music">&lt;&lt;&lt;</Link>
                <div className="protocol">
                    <div className="protocol-title"></div>
                    <div className="context">
                        <div></div>
                        <div></div>
                        <div>1)</div>
                        <div>2)</div>
                        <div>3)</div>
                        <div>4)</div>
                        <div>5)</div>
                        <div>6)</div>
                        <div>7)</div>
                        <div>8)</div>
                        <div>9)</div>
                        <div>10)</div>
                        <div>11)</div>
                    </div>
                    <div className="agree-line">
                        <div className="checkbox">
                            <input 
                                type="checkbox" 
                                id="agree"
                                checked={this.state.agree}
                                onChange={this.handleSelect}
                            />
                            <label htmlFor="agree"></label>
                        </div>
                        <span></span>
                        <Link 
                            to="/agreement" 
                            target="_blank" 
                            style={{
                                color: '#2d7aec',
                                textDecoration: 'none',
                            }}
                        ></Link>
                    </div>
                </div>
                <div className="form-item">
                    <div className="title">
                        <span className="name"></span>
                        <span className="required">*</span>
                        <span className="remind">(20)</span>
                    </div>
                    <div className="form-content">
                        <input 
                            className="input" 
                            type="text"
                            value={this.state.musicName}
                            onChange={this.handleMusicNameChange}
                        />
                        <span className="warn">{this.state.musicNameWarn}</span>             
                    </div>
                </div>
                <div className="form-item">
                    <div className="title">
                        <span className="name"></span>
                        <span className="required">*</span>
                        <span className="remind">(20)</span>
                    </div>
                    <div className="form-content">
                        <input 
                            className="input" 
                            type="text"
                            value={this.state.singer}
                            onChange={this.handleSingerChange}
                        />  
                        <span className="warn">{this.state.singerWarn}</span>
                    </div>           
                </div>
                <div className="form-item">
                    <div className="title">
                        <span className="name"></span>
                        <span className="required">*</span>
                        <span className="remind">(MP3,WMA,FLAC,M4A20M)</span>
                    </div>
                    <div className="upload-music">
                        <div className="upload-music-btn">
                            <span className="upload-btn-name"></span>
                            <input 
                                style={{opacity: '0'}}
                                value={undefined}
                                type="file"
                                onChange={this.handleMusicUpload}
                                accept=".mp3, .wma, .flac, .m4a"
                            />               
                        </div>
                        {this.state.uploadInfo ? <span className="remind">{this.state.uploadInfo}</span> : ''}
                        {this.state.fileWarn ? <span className="warn">{this.state.fileWarn}</span> : ''}
                    </div>
                </div>
                <div className="form-item">
                    <div className="title">
                        <span className="name"></span>
                        <span className="required">*</span>
                        <span className="remind">()</span>
                    </div>
                    <div className="form-content">                    
                        <Select 
                            handleSelectClick={this.handleSelectListClick}
                            pageSelectShow={this.state.pageSelectShow}
                            list={['','','']}
                            currentSelect={this.state.currentSelect}
                            handleCurrentSelectChange={this.handleCurrentSelectChange}
                        />
                        <span className="warn">{this.state.selectWarn}</span>
                    </div>               
                </div>
                <div 
                    className="form-item"
                    style={{
                        marginTop: '22px',
                        display: 'flex',
                        alignItems: 'center',
                    }}
                >   
                    {
                        !this.state.isLoading ? 
                        <div 
                            className="confirm-button"
                            onClick={this.handleUploadClick}
                        >
                            
                            <span
                                style={{
                                    background: `url(${upload}) center center/cover no-repeat`,
                                    marginLeft: '5px',
                                    width: '14px',
                                    height: '14px',
                                }}
                            ></span>
                        </div> :
                        <div 
                            className="confirm-button"
                            style={{
                                background: `url(${loading}) center center/100% no-repeat`,
                                position: 'relative',
                                overflow: 'hidden',
                                border: '1px solid #ffd900',
                                boxSizing: 'border-box',                        
                            }}
                        >   
                            <div
                                style={{
                                    position: 'absolute',
                                    backgroundColor: '#23252f',
                                    left: `${this.state.percent}%`,
                                    width: '100%',
                                    height: '100%',
                                    transition: 'left .5s',
                                }}
                            ></div>
                            <div
                                style={{position: 'absolute'}}
                            >
                                {`${this.state.percent.toFixed(0)}%`}
                            </div>
                        </div> 
                    }
                    {this.state.isLoading ? <div
                        style={{
                            fontSize: '12px',
                            color: '#ffd900',
                            marginLeft: '5px',
                        }}
                    >{this.state.loadingTip}</div> : ''}    
                </div>
            </div>
            <Modal 
                show={this.state.modalShow}
                info={this.state.modalInfo}
                close={this.modalClose}
            />
        </div>
    }
}

export default UploadBox;


